:root {
  --font-family: "Nunito Sans", sans-serif;
  --fs-homepage: 14px;
  --fs-detail: 16px;
  --fw-light: 300;
  --fw-semi-bold: 600;
  --fw-bold: 800;
  --clr-background: #fafafa;
  --clr-elements: #fff;
  --clr-text: #111517;
  --clr-input: #858585;
  --clr-shadow: #0000000d;
}

[data-theme="dark"] {
  --clr-background: #202c37;
  --clr-elements: #2b3945;
  --clr-text: #fff;
  --clr-input: #fff;
  --clr-shadow: #0000001a;
}

*, :before, :after {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  font-family: var(--font-family);
  font-size: var(--fs-homepage);
  font-weight: var(--fw-light);
  background-color: var(--clr-background);
  color: var(--clr-text);
  -webkit-font-smoothing: antialiased;
  min-height: 100vh;
  line-height: 1.5;
}

img, picture, video, canvas, svg {
  max-width: 100%;
  display: block;
}

input, button, textarea, select {
  font: inherit;
  color: inherit;
}

h1, h2, h3 {
  font-weight: var(--fw-bold);
}

a {
  color: inherit;
  text-decoration: none;
}

button {
  cursor: pointer;
}

.topbar {
  background-color: var(--clr-elements);
  width: 100%;
  box-shadow: 0 2px 4px var(--clr-shadow);
  justify-content: space-between;
  align-items: center;
  padding: 1.5rem 5vw;
  display: flex;
}

.topbar .left {
  font-weight: var(--fw-bold);
  font-size: clamp(1rem, 4vw, 1.5rem);
}

.topbar .right {
  font-weight: var(--fw-semi-bold);
  background: none;
  border: none;
  align-items: center;
  gap: .5rem;
  display: flex;
}

main.container {
  max-width: 1440px;
  margin-inline: auto;
  padding: 3rem clamp(1.25rem, 5vw, 4.5rem);
}

.country-details {
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  align-items: center;
  gap: 4rem;
  display: grid;
}

.country-flag {
  aspect-ratio: 4 / 3;
  object-fit: cover;
  width: 100%;
}

.country-name {
  margin-bottom: 2rem;
  font-size: 2rem;
}

.details-grid {
  grid-template-columns: 1fr;
  gap: 2rem;
  margin-bottom: 3rem;
  display: grid;
}

@media (width >= 960px) {
  .details-grid {
    grid-template-columns: 1fr 1fr;
  }
}

.info {
  line-height: 1.8;
  font-size: var(--fs-detail);
}

.label {
  font-weight: var(--fw-semi-bold);
  margin-right: .5ch;
}

.border-countries {
  flex-wrap: wrap;
  align-items: baseline;
  gap: 1rem;
  display: flex;
}

.border-countries-container {
  flex-wrap: wrap;
  gap: .75rem;
  display: flex;
}

.border-country, .back-button {
  background-color: var(--clr-elements);
  box-shadow: 0 1px 4px var(--clr-shadow);
  border: none;
  border-radius: 4px;
  padding: .5rem 1.5rem;
}

.back-button {
  margin-bottom: 4rem;
}

.input-container {
  flex-wrap: wrap;
  justify-content: space-between;
  gap: 2.5rem;
  margin-bottom: 3rem;
  display: flex;
}

.search-input-wrapper {
  box-shadow: 0 1px 4px var(--clr-shadow);
  background-color: var(--clr-elements);
  border-radius: 4px;
  align-items: center;
  width: 100%;
  max-width: 480px;
  display: flex;
  position: relative;
  overflow: hidden;
}

.search-icon {
  color: var(--clr-input);
  pointer-events: none;
  position: absolute;
  top: 50%;
  left: 2rem;
  transform: translateY(-50%);
}

.search-input {
  width: 100%;
  font-size: var(--fs-homepage);
  background: none;
  border: none;
  padding: 1.125rem 2rem 1.125rem 4.5rem;
}

.search-input::placeholder {
  color: var(--clr-input);
}

.filter-wrapper {
  background-color: var(--clr-elements);
  box-shadow: 0 1px 4px var(--clr-shadow);
  border-radius: 4px;
  min-width: 200px;
  position: relative;
}

.filter-wrapper:after {
  content: "▾";
  pointer-events: none;
  position: absolute;
  top: 50%;
  right: 1.5rem;
  transform: translateY(-50%);
}

.filter-select {
  appearance: none;
  cursor: pointer;
  background: none;
  border: none;
  width: 100%;
  padding: 1.125rem 1.5rem;
}

.filter-select option {
  background-color: var(--clr-elements);
  color: var(--clr-text);
}

.countries-grid {
  grid-template-columns: repeat(auto-fit, minmax(260px, 1fr));
  gap: 4.5rem;
  display: grid;
}

.country-card {
  background-color: var(--clr-elements);
  box-shadow: 0 1px 4px var(--clr-shadow);
  cursor: pointer;
  border-radius: 4px;
  transition: transform .2s ease-in-out;
  overflow: hidden;
}

.country-card:hover {
  transform: translateY(-5px);
}

.country-card .country-flag {
  aspect-ratio: 1.6;
  object-fit: cover;
  width: 100%;
}

.country-card .details-text-container {
  padding: 1.5rem 1.5rem 2.875rem;
}

.country-card .country-name {
  margin-bottom: 1rem;
  font-size: 1.125rem;
}

.country-card .info {
  line-height: 1.6;
  font-size: var(--fs-homepage);
}
